<template>
  <div class="users-detail">
    <transfer-chart-sum :user="user"></transfer-chart-sum>

    <div class="navbar">
      <router-link class="back" :to="{name: 'users'}">
        <i class="fa fa-angle-left fa-lg"></i>
      </router-link>
      <router-link class="edit" :to="{name: 'user-edit'}">
        <i class="fa fa-edit fa-lg"></i>
      </router-link>
    </div>

    <div class="weui-panel">
      <transfer-info
        :user="user"
        :loading="isLoading"
        :isprofile="false">
      </transfer-info>

      <div class="weui-panel__hd"></div>
      <div class="weui-panel__bd">
        <div class="weui-media-box weui-media-box_small-appmsg">
          <div class="weui-cells">
            <div class="weui-cell">
              <div class="weui-cell__bd">
                <p>SS </p>
              </div>
              <div class="weui-cell__ft">
                <i class="weui-loading" v-if="isLoading"></i>
                <span v-else>{{user.port}}</span>
              </div>
            </div>
            <div class="weui-cell">
              <div class="weui-cell__bd">
                <p>SS </p>
              </div>
              <div class="weui-cell__ft">
                <i class="weui-loading" v-if="isLoading"></i>
                <span v-else>{{user.password}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="weui-panel__hd"></div>
      <div class="weui-panel__bd">
        <div class="weui-media-box weui-media-box_small-appmsg">
          <div class="weui-cells">
            <div class="weui-cell">
              <div class="weui-cell__bd">
                <p></p>
              </div>
              <div class="weui-cell__ft">
                <i class="weui-loading" v-if="isLoading"></i>
                <span v-else>{{user.userId}}</span>
              </div>
            </div>
            <div class="weui-cell">
              <div class="weui-cell__bd">
                <p></p>
              </div>
              <div class="weui-cell__ft">
                <i class="weui-loading" v-if="isLoading"></i>
                <span v-else>{{user.name}}</span>
              </div>
            </div>
            <div class="weui-cell">
              <div class="weui-cell__bd">
                <p></p>
              </div>
              <div class="weui-cell__ft">
                <i class="weui-loading" v-if="isLoading"></i>
                <span v-else>{{user.isLocked? '' : ''}}</span>
              </div>
            </div>
            <div class="weui-cell">
              <div class="weui-cell__bd">
                <p></p>
              </div>
              <div class="weui-cell__ft">
                <i class="weui-loading" v-if="isLoading"></i>
                <span v-else>{{user.isAdmin? '' : ''}}</span>
              </div>
            </div>
            <div class="weui-cell">
              <div class="weui-cell__bd">
                <p></p>
              </div>
              <div class="weui-cell__ft">
                <i class="weui-loading" v-if="isLoading"></i>
                <span v-else>{{user.activeAt}}</span>
              </div>
            </div>
            <div class="weui-cell">
              <div class="weui-cell__bd">
                <p></p>
              </div>
              <div class="weui-cell__ft">
                <i class="weui-loading" v-if="isLoading"></i>
                <span v-else>{{user.registAt}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Api from '../../api'
import TransferChartSum from '../../components/Transfer/Chart/Sum'
import TransferInfo from '../../components/Transfer/Info'

export default {
  props: ['profile'],
  components: {
    TransferChartSum,
    TransferInfo
  },

  data () {
    return {
      user: {},
      userId: null,
      isLoading: false
    }
  },

  created () {
    this.userId = this.$route.params.userId
    this.fetch()
  },

  methods: {
    fetch () {
      this.isLoading = true
      let userId = this.userId
      Api('/api/users/detail', {query: {userId}}).then(({data}) => {
        this.isLoading = false
        this.user = data
      }).catch(() => {
        this.isLoading = false
      })
    }
  }
}
</script>

<style lang="less">
.users-detail {
  position: relative;

  .navbar {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;

    .back, .edit {
      position: absolute;
      top: 15px;
      color: #fff;
    }
    .back {
      left: 15px;
    }
    .edit {
      right: 15px;
    }
  }

  .weui-panel {
    margin-top: 0;
  }
}
</style>



